<template>
  <div class="hello">
    <input type="text" @input="changeStr" v-model="str">  
    <van-field v-model="str2" :formatter="formatter" label="文本" placeholder="请输入用户名" />
    <van-field v-model="str3" :formatter="formatter2" label="文本" placeholder="请输入用户名" />
    <van-field v-model="str4" :formatter="formatter3" label="文本" placeholder="请输入用户名" />
    <input type="file" id='check' multiple>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      str:'',
      str2:'',
      str3:'',
      str4:'',
      flag:true
    }
  },
  methods: {
    changeStr(e){
      if (!this.flag) return;
      this.str = e.target.value.replace(/[`～*~!@#$%^&*()_\-+=<>?:"{}|,./;'\\[\]·~！@#￥%……&*（）——\-+={}|《》？：“”【】、；‘’，。、]/g, '').replace(/\s/g, "");
      this.$nextTick(()=>{
        this.flag = true;
      })
    },
    formatter(value) {
      // 过滤输入的数字
      return value.replace(/[`～*~!@#$%^&*()_\-+=<>?:"{}|,./;'\\[\]·~！@#￥%……&*（）——\-+={}|《》？：“”【】、；‘’，。、]/g, '').replace(/\s/g, "");

    },
    formatter2(value) {
      // 过滤输入的数字
      return value.replace(/[`～*~!@#$%^&*()_\-+=<>?:"{}|,./;'\\[\]·~！@#￥%……&*（）——\-+={}|《》？：“”【】、；‘’，。、]/g, '');

    },
        formatter3(value) {
      // 过滤输入的数字
      return value.replace(/\s/g, "");

    },
  },
}
</script>

